.mobile {
    top: 0;
    right: 0;
    height: 85vh;
    padding: 30px;
    border-radius: 0 0 30px 30px;
    background-color: white;
    position: fixed;
    width: 100%;
    z-index: 2000;
    box-shadow: var(--black-shadow-3);
    opacity: 0;
    transform: translateY(-100%);
    transition: 0.3s ease;
}

.mobile--show {
    opacity: 1;
    transform: translateY(0);
}

.mobile__menu {
    list-style: none;
    display: flex;
    flex-direction: column;
    justify-content: center;
    gap: 30px;
    width: 100%;
    text-align: center;
}

.mobile__link {
    color: black;
    font-size: 1.6rem;
}
.mobile__link--active {
    font-weight: 500;
    border-bottom: 2px solid var(--red-accent);
}

.mobile__close {
    position: absolute;
    bottom: 20px;
    left: 50%;
    transform: translateX(-50%);
    font-size: 4rem;
    color: var(--red-accent);
}
