.sale {
    max-width: 95%;
    margin: auto;
    margin-top: calc(var(--sale-section-indent) * -1);
    text-align: center;
    padding: 30px 0;
    background-color: white;
    border-radius: 20px;
    box-shadow: var(--black-shadow-1);
    width: 100%;
    position: relative;
}
.sale__title {
    font-size: 1.6rem;
    font-weight: 600;
}
.sale__card {
    width: 100%;
    min-height: auto;
    display: flex;
    padding: 15px;
    flex-direction: column;
    align-items: center;
}
.sale__image-container {
    height: auto;
    display: flex;
    justify-content: center;
}
.sale__image {
    width: 100%;
    object-fit: cover;
}

/* ARROWS */
.slick-prev,
.slick-next {
    position: absolute;
    top: 50%;

    display: block;
    align-items: center;
    justify-content: center;

    width: 40px;
    height: 40px;
    padding: 0;
    -webkit-transform: translate(0, -50%);
    -ms-transform: translate(0, -50%);
    transform: translate(0, -50%);

    cursor: pointer;

    border: none;
    outline: none;
    background: white;
    box-shadow: var(--black-shadow-2);
    z-index: 10;
    border-radius: 50%;
}

.slick-icon {
    font-size: 1.7rem;
    transform: translateY(25%);
    transition: 0.3s ease;
}

/* RESPONSIVE */
@media screen and (max-width: 1000px) {
    .sale {
        max-width: calc(100% - 50px);
    }
    .sale__title {
        font-size: 1.2rem;
    }
}

/* SLICK */
.slick-prev {
    left: -20px;
}
.slick-next {
    right: -20px;
}

.slick-prev:hover,
.slick-next:hover {
    color: var(--red-accent);
}

/* ARROWS DISABLED */
.slick-prev.slick-disabled {
    opacity: 0;
    pointer-events: none;
}
.slick-next.slick-disabled {
    opacity: 0;
    pointer-events: none;
}

/* DOTS */
.slick-dotted.slick-slider {
    margin-bottom: 30px;
}

.slick-dots {
    position: absolute;
    bottom: -25px;

    display: block;

    width: 100%;
    padding: 0;
    margin: 0;

    list-style: none;

    text-align: center;
}
.slick-dots li {
    position: relative;

    display: inline-block;

    width: 20px;
    height: 20px;
    margin: 0 5px;
    padding: 0;

    cursor: pointer;
}
.slick-dots li button {
    font-size: 0;
    line-height: 0;

    display: block;

    width: 20px;
    height: 20px;
    padding: 5px;

    cursor: pointer;

    color: transparent;
    border: 0;
    outline: none;
    background: transparent;
}
.slick-dots li button:hover,
.slick-dots li button:focus {
    outline: none;
}
.slick-dots li button:hover:before,
.slick-dots li button:focus:before {
    opacity: 1;
}
.slick-dots li button:before {
    font-family: 'slick';
    font-size: 30px;
    line-height: 20px;

    position: absolute;
    top: 0;
    left: 0;

    width: 20px;
    height: 20px;

    content: '•';
    text-align: center;

    opacity: 0.25;
    color: black;

    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}
.slick-dots li.slick-active button:before {
    opacity: 0.75;
    color: black;
}
