@import url('https://fonts.googleapis.com/css2?family=Prompt:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');

:root {
    --header-height: 100px;
    --header-height-mobile: 70px;
    --sale-section-indent: 75px;

    --page-background: #eee;
    --red-accent: rgb(255, 19, 50);

    /* SHADOWS */
    --black-shadow-1: 0 0 20px 0 rgba(0, 0, 0, 0.1);
    --black-shadow-2: 0 0 20px 0 rgba(0, 0, 0, 0.2);
    --black-shadow-3: 0 0 20px 0 rgba(0, 0, 0, 0.3);
    --black-shadow-4: 0 0 20px 0 rgba(0, 0, 0, 0.4);
    --black-shadow-5: 0 0 20px 0 rgba(0, 0, 0, 0.5);
    --black-shadow-5: 0 0 20px 0 rgba(0, 0, 0, 0.6);
    --black-shadow-7: 0 0 20px 0 rgba(0, 0, 0, 0.7);
    --black-shadow-8: 0 0 20px 0 rgba(0, 0, 0, 0.9);
    --black-shadow-9: 0 0 20px 0 rgba(0, 0, 0, 1);
}

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

html,
body {
    font-family: 'Poppins', sans-serif;
    background-color: var(--page-background);
    height: 300vh;
}

a {
    text-decoration: none;
}

.section {
    width: 100%;
    text-align: center;
    margin-top: 175px;
}
.section-container {
    max-width: 1400px;
    margin: 0 auto;
    margin-top: 80px;
}

@media screen and (max-width: 1450px) {
    .section-container {
        padding: 0 25px;
    }
}

.section__title {
    font-size: 2rem;
    position: relative;
}
.section__title::after {
    content: '';
    width: 50px;
    height: 3px;
    position: absolute;
    bottom: -10px;
    left: 50%;
    background-color: var(--red-accent);
    transform: translateX(-50%);
    border-radius: 3px;
}
