.find__map {
    border: 0;
    width: 100%;
    margin-bottom: -20px;
    box-shadow: var(--black-shadow-1);
}

.find__container {
    display: flex;
    gap: 50px;
    justify-content: space-between;
}

.find__adress {
    text-align: right;
    min-width: max-content;
}
.find__adress-container > h3 {
    font-size: 1.6rem;
    font-weight: 500;
    margin-bottom: 2.4rem;
}
.find__adress-container > p {
    font-size: 1.2rem;
    font-weight: 500;
    opacity: 0.7;
    margin-top: 0.6rem;
}

.find__link {
    display: block;
    width: 100%;
    text-align: center;
    margin-top: 3rem;
    color: white;
    font-size: 1.3rem;
    background-color: var(--red-accent);
    padding: 10px 25px;
    transition: 0.3s ease;
}

.find__link:hover {
    border-radius: 10px;
}

@media screen and (max-width: 900px) {
    .find__container {
        flex-direction: column;
    }
    .find__adress {
        display: flex;
        justify-content: space-between;
        align-items: flex-start;
        min-width: none;
        width: 100%;
        text-align: left;
    }
    .find__link {
        width: 50%;
        margin: 0;
    }
    .find__adress-container > h3 {
        margin: 0;
        font-size: 1.3rem;
    }
    .find__adress-container > p {
        margin: 0;
        font-size: 1.1rem;
    }
}

@media screen and (max-width: 900px) {
    .find__adress {
        flex-direction: column;
        align-items: center;
        text-align: center;
        gap: 30px;
    }
    .find__map {
        margin-top: -20px;
        max-height: 400px;
    }
    .find__link {
        width: max-content;
    }
}
