.header {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    color: white;
    transform: translateY(0);
    transition: 0.3s ease;
    z-index: 1000;
}
.header--hide {
    transform: translateY(-100%);
}
.header--white {
    background-color: white;
    color: black;
    transform: translateY(0);
    box-shadow: 0 0 20px 0 rgba(0, 0, 0, 0.4);
}
.header__hamburger {
    font-size: 2.25rem;
    display: none;
    cursor: pointer;
    transition: 0.3s ease;
    -webkit-font-smoothing: antialiased;
}
.header__hamburger:hover {
    transform: scale(1.1);
}
.header__title {
    font-size: 2rem;
}
.header__nav {
    display: flex;
    align-items: center;
    justify-content: space-between;
    max-width: 1600px;
    margin: 0 auto;
    height: var(--header-height);
}

@media screen and (max-width: 1600px) {
    .header__nav {
        padding: 0 30px;
    }
}
@media screen and (max-width: 1000px) {
    .header__nav {
        padding: 0 30px;
    }
    .header__hamburger {
        display: initial;
    }
}
@media screen and (max-width: 768px) {
    .header__nav {
        padding: 0 30px;
        height: var(--header-height-mobile);
    }
    .header__title {
        font-size: 1.5rem;
    }
    .header__hamburger {
        display: initial;
    }
    .header__menu {
        display: none;
    }
}
