.header__menu {
    display: flex;
    gap: 50px;
    list-style: none;
}
.header__link {
    color: inherit;
    font-size: 1.2rem;
    padding: 5px 10px;
    margin-right: -10px;
    position: relative;
}
.header__link::after {
    content: '';
    position: absolute;
    left: 8px;
    bottom: 0;
    width: calc(100% - 16px);
    max-width: 0%;
    height: 1px;
    background-color: white;
    transition: 0.3s ease;
}
.header--white .header__link::after {
    background-color: black;
}
.header__link:hover:after {
    max-width: 100%;
}
.header__link--active {
    font-weight: 600;
    background-color: rgb(255, 19, 50);
    color: white;
    transition: 0.3s ease;
}
.header__link--active:hover {
    border-radius: 5px;
}
.header__link--active::after {
    display: none;
}

@media screen and (max-width: 1000px) {
    .header__menu {
        display: none;
    }
}
